.aboutMe {
  &__Image {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    & > img {
      border-radius: 50%;
      object-position: 52% 44%;
      object-fit: none;
      // background-position: top;
      width: 20rem;
      height: 20rem;
    }
    &__textContent {
      text-align: center;
    }
  }
  &__text {
    text-align: center;
    background-color: rgb(202, 201, 201);
    border: solid 1px grayscale($color: #4b4a4a);
    width: 70vh;
    height: 50vh;
  }
}
@media only screen and (min-width: 768px) {
  .aboutMe {
    display: flex;
    justify-content: center;
    margin-top: 5rem;
    gap: 5rem;
  }
}
